/* frontend/src/App.css */

.App {
  text-align: center;
  font-family: 'Arial, sans-serif';
  margin-top: 50px;
}

.title {
  font-size: 3em;
  color: #333;
  margin-bottom: 20px;
}

.button {
  background-color: #007BFF;
  color: white;
  padding: 10px 20px;
  font-size: 1.2em;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.button:hover {
  background-color: #0056b3;
}
